<template lang="pug">
include ../templates/button

.archive(:class="loaderClass")
  .preloader__spinner
      .preloader__animation
  transition(name="list")
    .hint(v-if="hint")
      |На этой странице находятся все клиенты, сотрудничество с которыми так или иначе не сложилось. Заметьте в этом приложении вообще нет кнопки "Удалить". Любого потерянного клиента можно вернуть в главную таблицу. Помните, каждый потерянный клиент это потерянная прибыль. Почитайте свои записи, оставленные к потерянному клиенту, посмотрите как развивалось Ваше сотрудничество, на странице "История". Может быть Вам удастся возобновить сотрудничество.
  .archive__search
    search
  .archive__menu
    ul.archive__menu-list
      li.archive__menu-item(data-role="eva-filter", data-status="all", @click="filterEvas('all')").archive__menu-item--active
        +button("text", "", "Все", "fa-users") 
      li.archive__menu-item(data-role="eva-filter", data-status="aspirant", @click="filterEvas('aspirant')") 
        +button("text", "", "Новые", "fa-user-plus") 
      li.archive__menu-item(data-role="eva-filter", data-status="current", @click="filterEvas('current')") 
        +button("text", "", "Открытые", "fa-user-check") 
      li.archive__menu-item(data-role="eva-filter", data-status="sleep", @click="filterEvas('sleep')") 
        +button("text", "", "Спящие", "fa-user-clock")
      li.archive__menu-item(data-role="eva-filter", data-status="favorite", @click="filterEvas('favorite')") 
        +button("text", "", "Постоянные", "fa-hands-helping")
      li.archive__menu-item(data-role="eva-filter", data-status="winner", @click="filterEvas('winner')") 
        +button("text", "", "V.I.P.", "fa-gem")
  transition(name="component-fade",  mode="out-in", v-for="eva in evasArray", v-bind:key="eva.id")
    .archive__item
        dark_eva(:eva="eva")
</template>

<script>
    import dark_eva from './dark-eva';
    import search from './search';
    import { mapState, mapActions } from 'vuex';
    import {eventBus} from '../main.js';
    export default {
      name: 'archive',
      data () {
        return {
          loaderClass: 'archive--loading',
          searchString: '',
          evasArray: []
        }
      },
      props: {
          hint: Boolean
      },
      computed: {
        ...mapState({
            dark_evas: state => state.dark_evas.dark_evas
        })
      },
      methods: {
         ...mapActions(['getAllDarkEvasFromApi']),
        filterEvas(name){
          const filters = document.querySelectorAll('[data-role="eva-filter"]');
          const element = document.querySelector(`[data-status="${name}"]`);
          const category =  element.dataset.status;
          for(const filter of filters){
            filter.classList.remove('archive__menu-item--active');
          }
          element.classList.add('archive__menu-item--active');
          if(category == "all"){
            this.evasArray = this.dark_evas;
          } else {
            const checkEvas = this.dark_evas.filter(eva => eva.status == category);
            this.evasArray = checkEvas;
          }
        },
        searchEvas(searchValue){
          if(searchValue == ""){
            this.evasArray = this.dark_evas;
          } else {
            const checkEvas = this.dark_evas.filter(
            eva => eva.name.toLowerCase().indexOf(searchValue) > -1 
            || eva.id.toLowerCase().indexOf(searchValue) > -1);
            this.evasArray = checkEvas;
          }
        }
      },
      mounted(){
        this.$nextTick(function () {
            this.getAllDarkEvasFromApi();
        });
        eventBus.$on('loadPage', data => {
          this.loaderClass = data.loaderClass;
          this.evasArray = this.dark_evas;
        }); 
        eventBus.$on('search', data => {
          this.searchString = data.searchString;
          this.searchEvas(this.searchString);
        });
      },
      components: {
        dark_eva,
        search
      }
    }
</script>
<style lang="scss" src="../assets/styles/elements/button.scss"></style>
<style lang="scss">
    @import '../assets/styles/layout/vars.scss';  
    .archive{
        display:grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 20px;
        align-self: flex-start;
        width:100%;
        min-height: 100vh;
        grid-auto-rows: max-content;
        .preloader__spinner{
            display: none;
        }
        .hint{
          grid-column: 1 / 5;
          padding-bottom: 0;
        }
    }
    .archive__menu{
      grid-column: 1 / 5;
    }
    .archive__search{
      grid-column: 1 / 5;
    }
    .archive__menu-list{
      display:grid;
      grid-template-columns: repeat(6, 1fr);
      grid-gap: 20px;
    }
    .archive__menu-item--active{
      .button--text{
        color: $mainColor;
        background: transparent;
      }
    }
    .archive--loading{
        background-color: transparent;
        .preloader__spinner{
            display: block;
        }
        .archive__item, .archive__menu, .archive__search{
            opacity:0;
            z-index:-2;
        }
    } 
</style>
<style lang="scss" src="../assets/styles/elements/preloader.scss"></style>
<style lang="scss" src="../assets/styles/elements/hint.scss"></style>


