<template lang="pug">
include ../templates/button

.eva(:id="eva.id", :class="{'dark-eva' : !isHistory || !eva.isActive}")
    .eva__id id: {{ eva.id }}
    .eva__avatar(:style="'background-image: url(' + eva.image + ');'")    
    .eva__name {{ eva.name }}
    .eva__charact
        .eva__title Кол-во заказов:
        .eva__raitng {{ eva.orders }}
    .eva__charact
        .eva__title Выплаты:
        .eva__raitng {{ eva.payouts }}$
    .eva__title(v-if="eva.status == 'aspirant' && isHistory") Cтатус: Новый клиент
    .eva__title(v-if="eva.status == 'sleep' && isHistory") Cтатус: Спящий клиент
    .eva__title(v-if="eva.status == 'current' && isHistory") Cтатус: Открытый клиент
    .eva__title(v-if="eva.status == 'favorite' && isHistory") Cтатус: Постоянный клиент
    .eva__title(v-if="eva.status == 'winner' && isHistory") Cтатус: V.I.P
    .eva__text-more(@click="toggleDescWindow" v-if="!isHistory || !eva.isActive")
        +button("text-light", "", "Описание", "fa-file-alt") 
    .eva__text-more(@click="toggleDescWindow" v-else)
        +button("text", "", "Описание", "fa-file-alt") 
</template>

<script>
    import edit_eva_btn from './edit-eva';
    import archive_btn from './archive-eva';
    import move from './move';
    import {eventBus} from '../main.js';
    export default {
      name: 'eva',
      props: {
        eva: Object,
        isHistory: Boolean
      },
      data () {
        return {
          msg: 'Welcome to Your Vue.js App'
        }
      },
      methods: {
          toggleDescWindow(event) {
              event.preventDefault();
              eventBus.$emit('callDescWindow', {visible: true, text: this.eva.text});
          }
      },
      components: {
        edit_eva_btn,
        archive_btn,
        move
      }
    }
</script>

<style lang="scss" src="../assets/styles/elements/button.scss"></style>
<style lang="scss" src="../assets/styles/elements/move.scss"></style>
<style lang="scss" src="../assets/styles/elements/penalty.scss"></style>
<style lang="scss">
    @import '../assets/styles/layout/vars.scss';    
    .eva{
        border-radius: 8px;
        background-color: $white;
        border: 2px solid $accentColor;
        padding: 15px;
        margin-bottom: 15px;
    }

    .dark-eva{
        background-color: $mainColor;
        color: $white;
        .move__item.move__opener{
            border:2px solid $white;
        }
        .move__menu{
            border:2px solid $white;
            border-top:none;
            margin-top:-2px;
        }
        .eva__text-more{
            color: $white;
        }
    }

    .eva__id{
        width: 100%;
        text-align: center;
        margin-bottom: 15px;
        font-family: $mainFont;
        font-size: 12px;
        font-weight: 400;
    }

    .eva__avatar{
        width: 150px;
        height: 150px;
        margin: 0 auto 15px;
        border-radius:50%;
        overflow: hidden;
        overflow: hidden;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }

    .eva__name{
        font-size: $h3fontSize;
        font-weight: 700;  
        margin-bottom: 20px; 
        text-align: center;
    }

    .eva__title{
        font-size: $h5fontSize;
        font-weight: 700;
        margin-bottom: 20px;
    }

    .eva__text-more{
        font-size: $h6fontSize;
        font-weight: 700;
        color: $mainColor;
        &:hover{
            text-decoration: none;
        }
    }

    .eva__star{
        font-size: $h3fontSize;
        color: $mainColor;
        transition: all .3s;
    }

    .eva__control-item{
        margin: 20px 0;
    }

    .eva__text{
        max-height: 170px;
        overflow: hidden;
    }

    .eva__raitng{
        display: flex;
        justify-content: space-between;
        margin: 15px 0;
        font-size: 26px;
        font-weight: bold;
        color: $accentColor;
    }
</style>


