<template lang="pug">
    include ../templates/button  
    .edit-eva(@click="openEvaform")
        +button("edit", "editEva", "Редактировать", "fa-user-edit")
</template>

<script>
    import {eventBus} from '../main.js';
    export default {
        name: 'eva',
        props: {
          eva: Object
        },
        methods: {
          openEvaform() {
              eventBus.$emit('openingEvaform', {visible: true, title: "Редактировать Клиента", isNewEva: false, eva: this.eva});
          }
        }
    }
</script>

<style lang="scss" src="../assets/styles/elements/button.scss"></style>


