import axios from 'axios';
import {eventBus} from '../../main.js';
const dark_evas = {
    state: {
        dark_evas: []
    },
    mutations: {
        addDarkEvas(state, data){
            state.dark_evas = data;
        }
    },
    actions: {
        getAllDarkEvasFromApi(store){
            axios
                .get('http://localhost:6060/dark-evas')
                .then(response => {
                    store.commit('addDarkEvas', response.data || []);
                })
                .catch(error => {
                    store.commit('addDarkEvas', []);
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'})
                })
                .then(() => {
                    eventBus.$emit('loadPage', {loaderClass: ''});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''})
                    }, 3000);
                });
        },
        updateDarkEva(store, eva){
            var data = new FormData();
            if(eva.file){
                data.append('file', eva.file);
            }
            data.append('name', eva.name);
            data.append('id', eva.id);
            data.append('status', eva.status);
            data.append('isActive', eva.isActive);
            data.append('orders', eva.orders);
            data.append('payouts', eva.payouts);
            data.append('penalty', eva.penalty);
            data.append('text', eva.text);
            data.append('image', eva.image);
            axios
                .post("http://localhost:6060/dark-eva", data, { headers: { 'Content-Type': 'multipart/form-data' }})
                .then(response => {
                    store.commit('addDarkEvas', response.data);
                    eventBus.$emit('showStatus', {text: 'Данные клиента изменены', visible: true, class: ''})
                })
                .catch(error => {
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'});
                })
                .then(() => {
                    eventBus.$emit('openingEvaform', {visible: false});
                    eventBus.$emit('showPreloader', {preloaderVisible: false});
                    eventBus.$emit('loadPage', {loaderClass: ''});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''});
                    }, 3000);
                });
        },
        moveDarkEva(store, eva){
            var data = new FormData();
            data.append('name', eva.name);
            data.append('id', eva.id);
            data.append('move', eva.move);
            data.append('isActive', eva.isActive);
            data.append('orders', eva.orders);
            data.append('payouts', eva.payouts);
            data.append('penalty', eva.penalty);
            data.append('text', eva.text);
            data.append('image', eva.image);
            axios
                .post("http://localhost:6060/moveInDark", data, { headers: { 'Content-Type': 'multipart/form-data' }})
                .then(response => {
                    store.commit('addDarkEvas', response.data);
                    eventBus.$emit('showStatus', {text: 'Данные о статусе клиента изменены, клиент покидает архив', visible: true, class: ''})
                })
                .catch(error => {
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'});
                })
                .then(() => {
                    eventBus.$emit('callArchiveWindow', {visible: false});
                    eventBus.$emit('showPreloader', {preloaderVisible: false});
                    eventBus.$emit('loadPage', {loaderClass: ''});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''});
                    }, 3500);
                }); 
        }
    }
}

export default dark_evas;


