<template lang="pug">
    .search
        input.eva-form__input(type="text", placeholder="Введите имя или id клиента...", @input="searchEvas($event)", v-model="searchString")
        .search__clean
            <svg @click="cleanSearch" class="close-icon" xmlns="http://www.w3.org/2000/svg" width="15" height="16" viewBox="0 0 15 16"><g><g><path class="close-icon close-icon--light" fill="#163850" d="M14.218 1.99a2 2 0 0 1 0 2.828L11.036 8l3.182 3.182a2 2 0 0 1 0 2.828l-.708.708a2 2 0 0 1-2.828 0L7.5 11.536l-3.182 3.182a2 2 0 0 1-2.828 0l-.708-.708a2 2 0 0 1 0-2.828L3.964 8 .782 4.818a2 2 0 0 1 0-2.828l.708-.708a2 2 0 0 1 2.828 0L7.5 4.464l3.182-3.182a2 2 0 0 1 2.828 0z"/></g></g></svg>
</template>

<script>
    import {eventBus} from '../main.js';
    export default {
        name: 'hint-switcher',
        data(){
            return {
                searchString: ''
            }
        },
        methods: {
          searchEvas() {
            eventBus.$emit('search', { searchString: this.searchString});
          },
          cleanSearch(){
              this.searchString = '';
              eventBus.$emit('search', { searchString: this.searchString});
          }
        }
    }
</script>

<style lang="scss">
    @import '../assets/styles/layout/vars.scss';  
    .search{
        position: relative;
    }
    .search__clean{
        position: absolute;
        top: -4px;
        right: -1px;
    }
</style>


