import axios from 'axios';
import {eventBus} from '../../main.js';

const history = {
    state: {
        evas: []
    },
    mutations: {
        addPastEvas(state, data){
            state.evas = data;
        }
    },
    actions: {
        getPastEvasFromApi(store){
            axios
                .get('http://localhost:6060/history-evas')
                .then(response => {
                    store.commit('addPastEvas', response.data || []);
                })
                .catch(error => {
                    store.commit('addPastEvas', []);
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'})
                })
                .then(() => {
                    eventBus.$emit('loadPage', {loaderClass: ''});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''})
                    }, 3000);
                });
        }
    }
};

export default history;


