import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

import app_table from './components/table';
import app_progress from './components/progress';
import archive from './components/archive';
import list from './components/list';
import rating from './components/rating';
import history from './components/history';

const routes = [
    {
        path: '/',
        component: app_table
    },
    {
        path: '/progress',
        component: app_progress
    },
    {
        path: '/archive',
        component: archive
    },
    {
        path: '/list',
        component: list
    },
    {
        path: '/rating',
        component: rating
    },
    {
        path: '/history',
        component: history
    }
];

export default new VueRouter({ routes, mode: 'history' });


