import axios from 'axios';
import {eventBus} from '../../main.js';
const evas = {
    state: {
        evas: []
    },
    mutations: {
        addEvas(state, data){
            state.evas = data;
        }
    },
    actions: {
        getAllEvasFromApi(store){
            axios
                .get('http://localhost:6060/evas')
                .then(response => {
                    store.commit('addEvas', response.data || []);
                })
                .catch(error => {
                    store.commit('addEvas', []);
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'})
                })
                .then(() => {
                    eventBus.$emit('loadPage', {loaderClass: ''});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''})
                    }, 3000);
                });
        },
        postNewEva(store, eva){
            var data = new FormData();
            if(eva.file){
                data.append('file', eva.file);
            }
            data.append('name', eva.name);
            data.append('status', eva.status);
            data.append('isActive', eva.isActive);
            data.append('orders', eva.orders);
            data.append('payouts', eva.payouts);
            data.append('penalty', eva.penalty);
            data.append('text', eva.text);
            data.append('image', eva.image);
            axios
                .post('http://localhost:6060/eva', data, { headers: { 'Content-Type': 'multipart/form-data' }})
                .then(response => {
                    store.commit('addEvas', response.data);
                    eventBus.$emit('showStatus', {text: 'Новый клиент добавлен', visible: true, class: ''});
                })
                .catch(error => {
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'});
                })
                .then(() => {
                    eventBus.$emit('openingEvaform', {visible: false});
                    eventBus.$emit('showPreloader', {preloaderVisible: false});
                    eventBus.$emit('loadPage', {loaderClass: ''});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''});
                    }, 3000);
                });
        },
        updateEva(store, eva){
            var data = new FormData();
            if(eva.file){
                data.append('file', eva.file);
            }
            data.append('name', eva.name);
            data.append('id', eva.id);
            data.append('status', eva.status);
            data.append('isActive', eva.isActive);
            data.append('orders', eva.orders);
            data.append('payouts', eva.payouts);
            data.append('penalty', eva.penalty);
            data.append('text', eva.text);
            data.append('image', eva.image);
            axios
                .post(`http://localhost:6060/updateEva`, data, { headers: { 'Content-Type': 'multipart/form-data' }})
                .then(response => {
                    store.commit('addEvas', response.data);
                    eventBus.$emit('showStatus', {text: 'Данные клиента изменены', visible: true, class: ''})
                })
                .catch(error => {
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'});
                })
                .then(() => {
                    eventBus.$emit('openingEvaform', {visible: false});
                    eventBus.$emit('showPreloader', {preloaderVisible: false});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''});
                    }, 3000);
                });
        },
        archiveEva(store, eva){
            var data = new FormData();
            data.append('name', eva.name);
            data.append('id', eva.id);
            data.append('status', eva.status);
            data.append('isActive', eva.isActive);
            data.append('orders', eva.orders);
            data.append('payouts', eva.payouts);
            data.append('penalty', eva.penalty);
            data.append('text', eva.text);
            data.append('image', eva.image);
            axios
                .post('http://localhost:6060/archive', data, { headers: { 'Content-Type': 'multipart/form-data' }})
                .then(response => {
                    store.commit('addEvas', response.data);
                    eventBus.$emit('showStatus', {text: 'Клиент добавлен в архив', visible: true, class: 'm-fail'})
                })
                .catch(error => {
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'});
                })
                .then(() => {
                    eventBus.$emit('callArchiveWindow', {visible: false});
                    eventBus.$emit('showPreloader', {preloaderVisible: false});
                    eventBus.$emit('loadPage', {loaderClass: ''});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''});
                    }, 3000);
                });
        },
        moveEva(store, eva){
            var data = new FormData();
            data.append('name', eva.name);
            data.append('id', eva.id);
            data.append('move', eva.move);
            data.append('isActive', eva.isActive);
            data.append('orders', eva.orders);
            data.append('payouts', eva.payouts);
            data.append('penalty', eva.penalty);
            data.append('text', eva.text);
            data.append('image', eva.image);
            axios
                .post("http://localhost:6060/move/", data, { headers: { 'Content-Type': 'multipart/form-data' }})
                .then(response => {
                    store.commit('addEvas', response.data);
                    eventBus.$emit('showStatus', {text: 'Данные о статусе клиента изменены', visible: true, class: ''});
                })
                .catch(error => {
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'});
                })
                .then(() => {
                    eventBus.$emit('callArchiveWindow', {visible: false});
                    eventBus.$emit('showPreloader', {preloaderVisible: false});
                    eventBus.$emit('loadPage', {loaderClass: ''});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''});
                    }, 3000);
                }); 
        }
    }
}

export default evas;


