import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

import evas from './modules/evas';
import dark_evas from './modules/dark-evas';
import progress from './modules/progress';
import history from './modules/history';

export const store = new Vuex.Store({
    modules: {
        evas,
        dark_evas,
        progress,
        history
    }
});


