import axios from 'axios';
import {eventBus} from '../../main.js';
const progress = {
    state: {all:0, sleepers: [], currents: [], favorites: [], dark_evas: [], winners: [], allEvas: []},
    mutations: {
        addProgress(state, data){
            state.all = data.all;
            state.sleepers = data.sleepers;
            state.currents = data.currents;
            state.favorites = data.favorites;
            state.dark_evas = data.dark_evas;
            state.winners = data.winners;
        },
        addAllEvas(state, data){
            state.allEvas = data;
        }
    },
    actions: {
        getProgressFromApi(store){
            axios
                .get('http://localhost:6060/progress')
                .then(response => {
                    store.commit('addProgress', response.data);
                })
                .catch(error => {
                    store.commit('addProgress', {all:0, sleepers: [], currents: [], favorites: [], dark_evas: [], winners: []});
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'})
                })
                .then(() => {
                    eventBus.$emit('loadPage', {loaderClass: ''});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''})
                    }, 3000);
                });
        },
        getAllFromApi(store){
            axios
                .get('http://localhost:6060/all-evas')
                .then(response => {
                    store.commit('addAllEvas', response.data);
                })
                .catch(error => {
                    store.commit('addAllEvas', []);
                    eventBus.$emit('showStatus', {text: error, visible: true, class: 'm-fail'})
                })
                .then(() => {
                    eventBus.$emit('loadPage', {loaderClass: ''});
                    setTimeout(() => {
                        eventBus.$emit('showStatus', {text: '', visible: false, class: ''})
                    }, 3000);
                });
        }
    }
}

export default progress;


