<template lang="pug">
    include ../templates/button  
    .archive-eva(@click="openArchiveWindow")
        +button("archive", "inArchive", "В архив", "fa-archive")
</template>

<script>
    import {eventBus} from '../main.js';
    export default {
        name: 'archive-btn',
        props: {
          eva: Object
        },
        methods: {
          openArchiveWindow() {
              eventBus.$emit('callArchiveWindow', {visible: true, eva: this.eva});
          }
        }
    }
</script>

<style lang="scss" src="../assets/styles/elements/button.scss"></style>


