<template lang="pug">
include ../templates/button
.rating(:class="loaderClass")
    .preloader__spinner
          .preloader__animation
    transition(name="list")
        .hint(v-if="hint") 
            |На этой странице показан рейтинг всех клиентов, а так-же процент от общей прибыли, которую принес Вам каждый из клиентов. 
    nav.rating__menu
        ul.rating__menu-list
            li.rating__menu-item.rating__menu-item--active(@click="filterEvas('all')", data-role="eva-filter", data-category="all")
                +button("text", "", "Все", "fas fa-users")              
            li.rating__menu-item(@click="filterEvas('active')", data-role="eva-filter", data-category="active")
                +button("text", "", "Активные", "fas fa-table")
            li.rating__menu-item(@click="filterEvas('dark')", data-role="eva-filter", data-category="dark")
                +button("text", "", "Потерянные", "fas fa-archive")  
    .rating__total-info Общая прибыль: {{ totalPayouts }}$
    ul.rating__list
        li.rating__item(v-for="(eva, index) in evasArray")
            .rating__position {{ index+1 }}
            transition(name="component-fade",  mode="out-in")
                rating_eva(:eva="eva", :key="eva.id", :procent="procent")
</template>

<script>
    import { mapState, mapActions } from 'vuex';
    import {eventBus} from '../main.js';
    import rating_eva from './rating-eva';
    export default {
        name: 'app',
        data () {
            return {
                evasArray: [],
                allMoney: 0,
                loaderClass: "rating--loading"
            }
        },
        props: {
            hint: Boolean
        },
        methods: {
            ...mapActions(['getAllFromApi']),
            filterEvas(name){
                const filters = document.querySelectorAll('[data-role="eva-filter"]');
                const element = document.querySelector(`[data-category="${name}"]`);
                for(const filter of filters){
                  filter.classList.remove('rating__menu-item--active');
                }
                element.classList.add('rating__menu-item--active');
                if(name == "all"){
                  this.evasArray = this.evas;
                } else if(name == "active"){
                  const checkEvas = this.evas.filter(eva => eva.isActive);
                  this.evasArray = checkEvas;
                } else if(name == "dark"){
                  const checkEvas = this.evas.filter(eva => !eva.isActive);
                  this.evasArray = checkEvas;
                }
            }
        },
        computed: {
            ...mapState({
                evas: state => state.progress.allEvas
            }),
            totalPayouts(){
                let allMoney = 0;
                for (const eva of this.evasArray) {
                    allMoney += Number(eva.payouts);
                }
                return allMoney;
            },
            procent(){
                var result = this.totalPayouts / 100;
                if(result <= 0){
                    return 0;
                } else {
                    return Number(result);
                }
            }
        },
        mounted(){
            this.$nextTick(function () {
                this.getAllFromApi();
            });
            eventBus.$on('loadPage', data => {
                const compareNumeric = (a, b) => {
                    return b.payouts - a.payouts;
                }
                this.evasArray = this.evas.sort(compareNumeric);
                this.loaderClass = data.loaderClass;
            }); 
        },
        components: {
            rating_eva
        }
    }
</script>

<style lang="scss">
    @import '../assets/styles/layout/vars.scss';  
    .rating{
        width: 100%;
        align-self: flex-start;
        .preloader__spinner{
            display: none;
        }
    }
    .rating__menu{
        width: 100%;
        margin-bottom: 20px;
        padding: 0 10%;
    }
    .rating__menu-list{
        display:grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 10%;
    }
    .rating__total-info{
        color: $mainColor;
        font-family: $mainFont;
        font-size: $h3fontSize;
        font-weight: 700;
        margin-bottom: 20px;
    }
    .rating__list{
        border-left: 2px solid $mainColor;
        border-bottom: 2px solid $mainColor;
        min-height: 90vh;
        padding: 0;
        margin: 0;
    }
    .rating__position{
        color: $mainColor;
        font-family: $mainFont;
        font-size: $h3fontSize;
        font-weight: 700;
    }
    .rating__item{
        margin-bottom: 50px;
        display: flex;
        width:100%;
        align-items: center;
        padding-left: 20px;
    }
    .rating__menu-item--active{
      .button--text{
        color: $mainColor;
        background: transparent;
      }
    }
    .rating--loading{
        background-color: transparent;
        .preloader__spinner{
            display: block;
        }
        .rating__menu, .rating__total-info, .rating__list{
            opacity:0;
            z-index:-2;
        }
    } 
</style>
<style lang="scss" src="../assets/styles/elements/preloader.scss"></style>
<style lang="scss" src="../assets/styles/elements/hint.scss"></style>


