<template lang="pug">
    ul.move
        li.move__item.move__opener <i class="fas fa-layer-group"></i> Поместить...
            ul.move__menu
                li(@click="setStatus", data-status="current", v-if="eva.status != 'current' && eva.status != 'favorite' && eva.status != 'winner' || !eva.isActive").move__item <i class="fas fa-user-check"></i> В открытые
                li(@click="setStatus", data-status="sleep", v-if="eva.status != 'sleep' && eva.status != 'aspirant' && eva.isActive").move__item <i class="fas fa-user-clock"></i> В спящие
                li(@click="setStatus", data-status="favorite", v-if="eva.status != 'favorite' && eva.status != 'aspirant' && eva.isActive").move__item <i class="fas fa-hands-helping"></i> В постоянные
                li(@click="setStatus", data-status="winner", v-if="eva.status != 'winner' && eva.status != 'aspirant' && eva.isActive").move__item <i class="fas fa-gem"></i> В V.I.P
</template>

<script>
    import { mapActions } from 'vuex';
    import {eventBus} from '../main.js';
    export default {
      name: 'move',
      props: {
        eva: Object
      },
      methods: {
        ...mapActions(['moveEva', 'moveDarkEva']),
        setStatus(e){
          var eva = this.eva;
          eva.move = e.target.getAttribute('data-status');
          eventBus.$emit('showPreloader', {preloaderVisible: true});
          this.$nextTick(function () {
            if(eva.isActive){
              this.moveEva(eva);
            } else {
              eva.isActive = true;
              this.moveDarkEva(eva);
            }
          });
        }
      }
    }
</script>
<style lang="scss" src="../assets/styles/elements/move.scss"></style>


