<template lang="pug">
  .app-container
    transition(name="component-fade",  mode="out-in")
      .preloader(v-if="preloaderVisible")
          .preloader__spinner
            .preloader__animation
    .message(v-if="messageVisible", :class="messageClass") {{ messageText }}
    h1.app-title Clientbase
      .wolf
    .scroll-up(@click="scrollTop") <i class="fas fa-arrow-up"></i>
    .app-grid
      .app-grid__sidebar(:class="{'app-grid__sidebar--open' : menuVisible}")
        app_menu
        .app-grid__bottom-sidebar
          add_eva_btn
        .app-grid__bottom-sidebar
          .btn_mnu(:class="{'active' : menuVisible}", @click="menuVisible = !menuVisible")
            .btn_row
            .btn_row
            .btn_row
          hintSwitcher(:hint="isHint")
      .app-grid__content
        transition(name="component-fade",  mode="out-in")
          router-view(:hint="isHint")
    transition(name="component-fade",  mode="out-in")
      windowDesc
    transition(name="component-fade",  mode="out-in")
      evaForm
    transition(name="component-fade",  mode="out-in")
      windowArch
</template>

<script>
import app_menu from './components/app-menu';
import app_table from './components/table';
import app_progress from './components/progress';
import archive from './components/archive';
import add_eva_btn from './components/add-eva';
import windowDesc from './components/window-description';
import evaForm from './components/eva-form';
import windowArch from './components/window-archive';
import hintSwitcher from './components/hint-switcher';
import {eventBus} from './main.js';
export default {
  name: 'app',
  data () {
    return {
      messageText: "Какой-то текст ошибки",
      messageVisible: false,
      messageClass: '',
      preloaderVisible: false,
      isHint: true,
      time: 0,
      menuVisible: false
    }
  },
  methods: {
    scrollTop(){
      var top = Math.max(document.body.scrollTop,document.documentElement.scrollTop); 
      if(top>0) { 
        window.scrollTo(0,Math.floor(top/1.1)); 
        this.time = setTimeout(this.scrollTop, 15); 
      } else { 
        clearTimeout(this.time); 
      } 
      return false;
    }
  },
  mounted(){
    eventBus.$on('showStatus', data => {
      this.messageText = data.text;
      this.messageVisible = data.visible;
      this.messageClass = data.class;
    });
    eventBus.$on('showPreloader', data => {
      this.preloaderVisible = data.preloaderVisible;
    });
    eventBus.$on('switchHint', data => {
      this.isHint = data.active;
    });
  },
  components: {
    app_menu,
    app_table,
    app_progress,
    archive,
    add_eva_btn,
    windowDesc,
    evaForm,
    windowArch,
    hintSwitcher
  }
}
</script>

<style lang="scss" src="./assets/styles/layout/fonts.scss"></style>
<style lang="scss" src="./assets/styles/layout/base.scss"></style>
<style lang="scss" src="./assets/styles/elements/menu-btn.scss"></style>
<style lang="scss">
  @import './assets/styles/layout/vars.scss';  
  .app-container{
    position: relative;
  }
  .app-grid{
    display:grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 20px;
  }
  .app-grid__content{
    min-height: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    grid-column-start: 2;
    grid-column-end: 7;
    margin-bottom: 20px;
 }
 .app-title{
   text-align:center;
   margin-bottom:20px;
   font-family: 'Dancing Script', cursive;
   font-size:80px;
   line-height:1em;
   color:$accentColor;
 }
 .app-grid__bottom-sidebar{
   margin-top:20px;
 }
 .wolf{
   position: absolute;
   top:20px;
   right:20px;
   color:$mainColor;
 }
 .component-fade-enter-active, .component-fade-leave-active {
   transition: opacity .2s ease;
 }
 .component-fade-enter, .component-fade-leave-to{
   opacity: 0;
 }
 .message{
    position: fixed;
    top:20px;
    right: 20px;
    display: inline-block;
    padding: 20px;
    background-color: #44C21A;
    border-radius:15px;
    box-shadow: 0 0 15px 5px #ffce13;
    font-size: 18px;
    color: #fff;
    font-family: $mainFont;
    z-index: 10000;
    &.m-fail{
        background-color: red;
        box-shadow: 0 0 15px 5px #000;
    }
}

.scroll-up{
  position: fixed;
  bottom:50px;
  left: 50px;
  font-size: 32px;
  cursor: pointer;
  transition: all .3s;
  z-index: 9999;
  &:hover{
    color:$accentColor;
  }
}
</style>


