<template lang="pug">
.rating-eva(:class="{active : eva.isActive, dark : !eva.isActive}")
    .rating-eva__info
        .rating-eva__id {{ eva.id }}
        .rating-eva__name {{ eva.name }}
        .rating-eva__avatar(:style="'background-image: url(' + eva.image + ');'")   
        .rating-eva__payouts {{ eva.payouts }}$
    .rating-eva__procent-line
        .rating-eva__progress(:style="'width:' + evaProcent + '%;'")
        .rating-eva__procents {{ evaProcent }}%
</template>

<script>
    export default {
        name: 'app',
        props: {
            eva: Object,
            procent: Number
        },
        computed: {
            evaProcent(){
                var result = (this.eva.payouts / this.procent).toFixed(2);
                if(isNaN(result)){
                    return 0;
                } else {
                    return result;
                }
            }
        }
    }
</script>

<style lang="scss">
    @import '../assets/styles/layout/vars.scss';  
    .rating-eva{
        display:flex;
        align-items: center;
        width:100%;
        &.active{
            .rating-eva__progress{
                background-color: $accentColor;
                height: 100px;
            }
        }
        &.dark{
            .rating-eva__progress{
                background-color: $mainColor;
                height: 100px;
            }
        }
    }
    .rating-eva__info{
        width: 150px;
        text-align: center;
        margin-right: 20px;
        margin-left: 20px;
    }
    .rating-eva__procent-line{
        width: 85%;
        display: flex;
        align-items: center;
    }
    .rating-eva__avatar{
        margin: 0 auto;
        width: 150px;
        height: 150px;
        border-radius:50%;
        overflow: hidden;
        margin-bottom: 20px; 
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }
    .rating-eva__id{
        width: 100%;
        text-align: center;
        font-family: $mainFont;
        font-size: 12px;
        font-weight: 400;
    }
    .rating-eva__name{
        font-size: $h3fontSize;
        font-weight: 700;  
        margin-bottom: 20px; 
        text-align: center;
    }
    .rating-eva__payouts{
        font-size: 26px;
        font-weight: bold;
        color: $accentColor;
        text-align: center;
    }
    .rating-eva__procents{
        text-align: center;
        font-size: $h5fontSize;
        font-weight: 700;
        margin-left: 20px;
    }
</style>


