<template lang="pug">
    .table(:class="loaderClass")
        .preloader__spinner
            .preloader__animation
        transition(name="list")
            .hint(v-if="hint") 
                |На этой страницы находятся клиенты с которыми вы на данный момент работаете. 
                |Каждый клиент относится к той или иной категории. 
                |Чтобы узнать подробнее об этих категориях нажмите на подсказки рядом с названием этой категории.
                |Клиентов, сотрудничество с которыми было разорванно, стоит добавить в архив. 
                |Важный совет. Прежде чем перемещать клиента из одной категории в другую, оставляйте запись почему вы это сделали и не забывайте сохранить.
                |Это поможет, Вам увидеть точную ретроспективу Вашего сотрудничества на странице "История".
        .table__search
            search
        .table__item
            .table__heading 
                |<i class="fas fa-user-plus"></i> 
                span Новые ({{ aspirantNumber }})
                img.hint-opener(src="/dist/question-circle-solid.svg", @click="toggleDescWindow(aspirantText)")
            .table__content
                eva(v-for="eva in evasArray", v-if="eva.status=='aspirant'", :eva="eva", v-bind:key="eva.id")
        .table__item
            .table__heading 
                <i class="fas fa-user-clock"></i> 
                span Спящие ({{ sleepNumber }})
                img.hint-opener(src="/dist/question-circle-solid.svg", @click="toggleDescWindow(sleepText)")
            .table__content
                eva(v-for="eva in evasArray", v-if="eva.status=='sleep'", :eva="eva", v-bind:key="eva.id")
        .table__item
            .table__heading 
                <i class="fas fa-user-check"></i> 
                span Открытые ({{ currentNumber }})
                img.hint-opener(src="/dist/question-circle-solid.svg", @click="toggleDescWindow(currentText)")
            .table__content
                eva(v-for="eva in evasArray", v-if="eva.status=='current'", :eva="eva", v-bind:key="eva.id")
        .table__item
            .table__heading 
                <i class="fas fa-hands-helping"></i> 
                span Постоянные ({{ favoriteNumber }})
                img.hint-opener(src="/dist/question-circle-solid.svg", @click="toggleDescWindow(favoriteText)")
            .table__content
                eva(v-for="eva in evasArray", v-if="eva.status=='favorite'",  :winners="winnerNumber", :eva="eva", v-bind:key="eva.id")
        .table__item
            .table__heading 
                <i class="fas fa-gem"></i> 
                span V.I.P. ({{ winnerNumber }})
                img.hint-opener(src="/dist/question-circle-solid.svg", @click="toggleDescWindow(winnerText)")
            .table__content
                eva(v-for="eva in evasArray", v-if="eva.status=='winner'", :eva="eva", v-bind:key="eva.id")
</template>

<script>
    import eva from './eva';
    import search from './search';
    import { mapState, mapActions } from 'vuex';
    import {eventBus} from '../main.js';
    export default {
      name: 'app_table',
      data(){
          return {
            loaderClass: 'table--loading',
            evasArray: [],
            searchString: '',
            aspirantText: "В этой категории находятся все клиенты, с которыми, Вы, только установили, контакт, но пока еще не начали сотрудничество. Ваша, задача на данном этапе, убедить Вашего потенциального клиента, в том, что именно Вы сможете решить его проблему, и что сотрудничество именно с Вами, будет самым комфортным и продуктивным.",
            sleepText: "В этой категории находятся клиенты, которые уже воспользовались Вашими услугами, но дальнейшее сотрудничество пока не актуально. Большая ошибка многих предпринимателей в том, что они забывают про этих клиентов. После того как сотрудничество временно прекратилось, нужно периодически напоминать о себе, спрашивать о том, доволен ли клиент оказанной ранее услугой. Даже если клиент больше и не будет обращаться к Вам, он оценит заботу и внимание, и возможно приведет, Вам новых клиентов.",
            currentText: "В этой категории находятся клиенты, с которыми вы договорились о начале сотрудничества, но пока еще не закрыли сделку. Этим клиентам надо уделить особое внимание, так как от этого этапа зависит Ваши дальнейшие деловые отношения и то, какой доход вы получите от клиента. Постарайтесь действовать про активно. Ваша задача, чтобы клиент почувствовал, что получил гораздо лучший результат, чем ожидал, и за который платил.</br> Если после закрытия сделки, клиент временно не нуждается в Ваших услугах, его стоит добавить в категорию 'Спящие', если он намеревается, постоянно к Вам обращаться или продолжает сотрудничество, то его стоит переместить в категорию 'Постоянные'. Если выплаты, которые вы получили от клиента, составляют значительную долю от Вашей общей прибыли(можно посмотреть на странице 'Рейтинг'), то Вам повезло. У вас появился V.I.P. клиент.",
            favoriteText: 'В этой категории находятся Ваши постоянные клиенты. Как уже понятно из названия, это те клиенты, которые обращаются к Вам за услугой, на постоянной основе. С этими клиентами, вы не обязательно закрываете крупные сделки, но они обращаются к Вам постоянно. Важно стимулировать этих клиентов на то, чтобы они не прекращали обращаться именно к Вам. В тяжелые для Вашего бизнеса времена, эти клиенты помогут Вам держаться на плаву. Важно их не потерять.',
            winnerText: 'В этой категории находятся самые важные клиенты. Это клиенты, прибыль от сделок с которыми может составлять, значительную часть Вашего дохода. Сюда следует помещать клиентов, которые находятся на вершине рейтинга(можно посмотреть на странице "рейтинг") или тех клиентов, которые имеют для Вас особое значение. Терять таких клиентов нельзя ни в коем случае, так как они приносят основной доход Вашему бизнесу.'
          }
      },
      props: {
          hint: Boolean
      },
      computed: {
        ...mapState({
            evas: state => state.evas.evas
        }),
        aspirantNumber(){
            var result = 0;
            var evas = this.evas;
            for(let eva of evas){
                if(eva.status == 'aspirant'){
                    result++;
                }
            }
            return result;
        },
        sleepNumber(){
            var result = 0;
            var evas = this.evas;
            for(let eva of evas){
                if(eva.status == 'sleep'){
                    result++;
                }
            }
            return result;
        },
        currentNumber(){
            var result = 0;
            var evas = this.evas;
            for(let eva of evas){
                if(eva.status == 'current'){
                    result++;
                }
            }
            return result;
        },
        favoriteNumber(){
            var result = 0;
            var evas = this.evas;
            for(let eva of evas){
                if(eva.status == 'favorite'){
                    result++;
                }
            }
            return result;
        },
        winnerNumber(){
            var result = 0;
            var evas = this.evas;
            for(let eva of evas){
                if(eva.status == 'winner'){
                    result++;
                }
            }
            return result;
        }
      },
      methods: {
         ...mapActions(['getAllEvasFromApi']),
        searchEvas(searchValue){
          if(searchValue == ""){
            this.evasArray = this.evas;
          } else {
            const checkEvas = this.evas.filter(
            eva => eva.name.toLowerCase().indexOf(searchValue) > -1 
            || eva.id.toLowerCase().indexOf(searchValue) > -1);
            this.evasArray = checkEvas;
          }
        },
        toggleDescWindow(text) {
            eventBus.$emit('callDescWindow', {visible: true, text: text});
        }
      },
      mounted(){
        this.$nextTick(function () {
            this.getAllEvasFromApi();
        });
        eventBus.$on('loadPage', data => {
          this.loaderClass = data.loaderClass;
          this.evasArray = this.evas;
        }); 
        eventBus.$on('search', data => {
          this.searchString = data.searchString;
          this.searchEvas(this.searchString);
        }); 
      },
      components: {
        eva,
        search
      }
    }
</script>

<style lang="scss">
    @import '../assets/styles/layout/vars.scss';  
    .table{
        display: grid;
        width: 100%;
        grid-template-columns: repeat(5, 1fr);
        border-radius: 4px;
        transition: all .4s;
        .preloader__spinner{
            display: none;
        }
    }

    .table--loading{
        background-color: transparent;
        .preloader__spinner{
            display: block;
        }
        .table__item, .table__search{
            opacity:0;
            z-index:-2;
        }
    } 

    .table__search{
      grid-column: 1 / 6;
      height:auto;
      padding-bottom: 20px;
      background:#ebebeb;
    }

    .table__item{
        border:2px solid white;
        border-right: none;
        height: 90vh;
        position: relative;
        &:last-of-type{
            border-right:2px solid white;
        }
    }

    .table__content{
        height: calc(100% - 3.44rem);
        overflow-y: auto;
        margin-top: 55px;
    }

    .table__heading {
        background-color: $accentColor;
        height: 50px;
        text-align: center;
        line-height: 50px;
        font-size: $h5fontSize;
        margin-bottom: 20px;
        position: absolute;
        width: 100%;
        color: $mainColor;
        display: flex;
        justify-content: center;
        align-items: center;
        span{
            margin: 0 10px;
        }
    }
</style>
<style lang="scss" src="../assets/styles/elements/preloader.scss"></style>
<style lang="scss" src="../assets/styles/elements/hint.scss"></style>
<style lang="scss" src="../assets/styles/grid/desktop-min.scss"></style>


