<template lang="pug">
    include ../templates/button  
    .add-eva(@click="openEvaform")
        +button("add", "addEva", "Добавить", "fa-user-plus") 
</template>

<script>
    import {eventBus} from '../main.js';
    export default {
        name: 'add-Eva',
        methods: {
          openEvaform() {
            eventBus.$emit('openingEvaform', {
                  visible: true, 
                  title: "Добавить Клиента",
                  isNewEva: true,
                  eva: {
                    status: "aspirant",
                    name: "",
                    isActive: true,
                    orders: "0",
                    payouts: "0",
                    penalty: 0,
                    text: "",
                    image: "/dist/male.jpg"
                  }
            });
          }
        }
    }
</script>

<style lang="scss" src="../assets/styles/elements/button.scss"></style>


