<template lang="pug">
include ../templates/button

.history(:class="loaderClass")
  .preloader__spinner
      .preloader__animation
  transition(name="list")
    .hint(v-if="hint") 
      |На этой странице показана вся история сотрудничества со всеми клиентами. 
      |Введите в поиск имя или id клиента, чтобы увидеть историю сотрудничества только с данным клиентом.
      |Важный совет. Прежде чем перемещать клиента из одной категории в другую, оставляйте запись почему вы это сделали и не забывайте сохранить.
      |Это поможет, Вам увидеть точную ретроспективу Вашего сотрудничества.
  .history__search
    search
  transition(name="component-fade",  mode="out-in", v-for="eva in evasArray", v-bind:key="eva.id")
    .history__item
        past_eva(:eva="eva", :isHistory="isHistory")
</template>

<script>
    import past_eva from './past-eva';
    import search from './search';
    import { mapState, mapActions } from 'vuex';
    import {eventBus} from '../main.js';
    export default {
      name: 'history',
      data () {
        return {
          loaderClass: 'history--loading',
          evasArray: [],
          searchString: '',
          isHistory: true
        }
      },
      props: {
          hint: Boolean
      },
      computed: {
        ...mapState({
            evas: state => state.history.evas
        })
      },
      methods: {
         ...mapActions(['getPastEvasFromApi']),
        searchEvas(searchValue){
          if(searchValue == ""){
            this.evasArray = this.evas;
          } else {
            const checkEvas = this.evas.filter(
            eva => eva.name.toLowerCase().indexOf(searchValue) > -1 
            || eva.id.toLowerCase().indexOf(searchValue) > -1);
            this.evasArray = checkEvas;
          }
        }
      },
      mounted(){
        this.$nextTick(function () {
            this.getPastEvasFromApi();
        });
        eventBus.$on('loadPage', data => {
          this.loaderClass = data.loaderClass;
          this.evasArray = this.evas;
        }); 
        eventBus.$on('search', data => {
          this.searchString = data.searchString;
          this.searchEvas(this.searchString);
        });
      },
      components: {
        past_eva,
        search
      }
    }
</script>
<style lang="scss" src="../assets/styles/elements/button.scss"></style>
<style lang="scss">
    @import '../assets/styles/layout/vars.scss';  
    .history{
        display:grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 20px;
        align-self: flex-start;
        width:100%;
        min-height: 100vh;
        grid-auto-rows: max-content;
        .preloader__spinner{
            display: none;
        }
        .hint{
          grid-column: 1 / 5;
          padding-bottom: 0;
        }
    }
    .history__item{
      margin-bottom: -15px;
    }
    .history__search{
      grid-column: 1 / 5;
    }
    .history__menu-list{
      display:grid;
      grid-template-columns: repeat(6, 1fr);
      grid-gap: 20px;
    }
    .history__menu-item--active{
      .button--text{
        color: $mainColor;
        background: transparent;
      }
    }
    .history--loading{
        background-color: transparent;
        .preloader__spinner{
            display: block;
        }
        .history__item, .history__menu, .history__search{
            opacity:0;
            z-index:-2;
        }
    } 
</style>
<style lang="scss" src="../assets/styles/elements/preloader.scss"></style>
<style lang="scss" src="../assets/styles/elements/hint.scss"></style>


